import styled from 'styled-components';

export const CardBody = styled.div`
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 20px;

  @media only screen and (max-width: 500px) {
    padding-bottom: 5px;
  }
`;

export default CardBody;



